import React, { useState } from "react";
// import style from "./Account.module.css";
import DeleteAcnt from "./DeleteAcnt";
import Recovery from "./Recovery";
import AccountDelete from "./AccountDelete";

function Account({ dashboardData, dashboardFetch }) {
  const [switched, setSwitched] = useState(false);
  const [accountDelete, setAccountDelete] = useState(false);
  const switchh = () => {
    setSwitched(true);
  };
  const onAccountAction = () => {
    setAccountDelete((pre) => !pre);
  };
  return (
    <>
      {!switched && !accountDelete && (
        <Recovery
          switchh={switchh}
          dashboardData={dashboardData}
          dashboardFetch={dashboardFetch}
          onAccountAction={onAccountAction}
        />
      )}
      {accountDelete && <AccountDelete dashboardData={dashboardData} />}
      {switched && <DeleteAcnt dashboardData={dashboardData} />}
    </>
  );
}

export default Account;
